import React from "react"

import Icons from "../components/Icons"

import addToMailchimp from "gatsby-plugin-mailchimp"

class ContactForm extends React.Component {
  state = {
    name: null,
    email: null,
  }

  _handleChange = e => {
    console.log({
      [`${e.target.name}`]: e.target.value,
    })
    this.setState({
      [`${e.target.name}`]: e.target.value,
    })
  }

  _handleSubmit = e => {
    e.preventDefault()
    addToMailchimp(this.state.email, this.state)
      .then(({ msg, result }) => {
        if (result === "success") {
          throw msg
        }
        alert(msg)
        //alert(msg)
      })
      .catch(err => {
        alert(err)
      })
  }

  render() {
    return (
      <>
        <form
          name="contato"
          method="post"
          netlify-honeypot="bot-field"
          data-netlify="true"
          onSubmit={this._handleSubmit}
        >
          <input type="hidden" name="form-name" value="contato" />

          <div className={"container"}>
            <div className={"input_form"}>
              <input
                type="text"
                onChange={this._handleChange}
                name="name"
                id="name"
                placeholder="Seu nome"
              />
              <input
                type="email"
                onChange={this._handleChange}
                name="email"
                id="email"
                placeholder="Email"
              />
              <textarea
                name="message"
                id="message"
                placeholder="Escreva sua mensagem"
              />
            </div>
            <div className={"data_form"}>
              <h2>Contate-nos!</h2>
              <p>
                Gostaríamos muito de ouvir o que você tem a dizer.
                <br />
                Entraremos em contato com você dentro de 24 horas!
                <br />
                Fique ligado na sua caixa de emails.
              </p>
              <ul className={"social"}>
                <li>
                  <a href="https://fb.me/allto.digital" title={"Facebook"}>
                    <Icons.Facebook />
                  </a>
                </li>
                <li>
                  <a
                    href="https://www.instagram.com/allto.digital/"
                    title={"Instagram"}
                  >
                    <Icons.Instagram />
                  </a>
                </li>
                <li>
                  <a
                    href="https://www.linkedin.com/company/allto-digital"
                    title={"Linedkin"}
                  >
                    <Icons.Linkedin />
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <button id="btn_send" type="submit">
            <span class="noselect">Enviar</span>
            <div id="circle" />
          </button>
        </form>
      </>
    )
  }
}

export default ContactForm
